"use client";
import React, { useEffect, useState } from "react";
import { Container, Field } from "./InfoCard.styles";
import { H2 } from "@/components/Typography/Heading.styles";
import { Body3 } from "@/components/Typography/Body.styles";
import Image from "next/image";

import FacebookIC from "@/assets/svg/fb";
import TwitterIC from "@/assets/svg/x";
import IGIC from "@/assets/svg/ig";
import LinkedinIC from "@/assets/svg/linkedin";
import TextDecor from "@/assets/svg/textArea";

interface InfoProps {
  fullname: string;
  biography: string;
  contact: ContactProps[];
  interest: InterestProps[];
  avatar: AvatarProps;
}

interface ContactProps {
  id: number;
  platform: string;
  url: string;
}

interface InterestProps {
  id: number;
  interest: string;
}

interface AvatarProps {
  id: string;
  url: string;
  name: string;
  formats?: {
    thumbnail?: { url: string };
    small?: { url: string };
    medium?: { url: string };
    large?: { url: string };
  };
}

interface InfoCardProps {
  isNavbar: boolean;
  textColor?: string;
}

const InfoCard = ({ isNavbar, textColor }: InfoCardProps) => {
  const [info, setInfo] = useState<InfoProps[] | null>(null);
  const [loading, setLoading] = useState(false);
  const handleGetInfo = async () => {
    setLoading(true);
    try {
      const response = await fetch(
        "http://localhost:1337/api/authors?populate=*",
        { method: "GET" }
      );

      const data = await response.json();

      if (!response.ok) {
        setLoading(false);
      }

      setInfo(data.data);
    } catch (error) {
      console.log("Error: " + error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    handleGetInfo();
  }, []);

  return (
    <>
      {info?.map((item, index) => (
        <Container $isNavbar={isNavbar} key={index}>
          <div className="relative sm:w-[220px] w-[80px] sm:h-[220px] h-[80px]">
            <Image
              className=" rounded-full"
              src={`http://localhost:1337${item.avatar.url}`}
              alt="image"
              objectFit="cover"
              fill
            />
          </div>
          <div className="flex flex-col">
            <H2 $color={textColor}>my {item.fullname} diary</H2>
            <Field $isNavbar={isNavbar}>
              {item.interest.map((item, index) => (
                <Body3 $color={textColor} key={index}>
                  {item.interest}
                </Body3>
              ))}
            </Field>
          </div>
          <div className="hidden sm:flex flex-col gap-[6px] items-center">
            <div className="h-1.5"></div>
            <Body3 $align="center">{item.biography}</Body3>
            <Field>
              {item.contact.map((item, index) => (
                <a href={item.url} key={index}>
                  {item.platform === "fb" ? (
                    <FacebookIC />
                  ) : item.platform == "ig" ? (
                    <IGIC />
                  ) : item.platform === "x" ? (
                    <TwitterIC />
                  ) : (
                    <LinkedinIC />
                  )}
                </a>
              ))}
            </Field>
          </div>
        </Container>
      ))}
    </>
  );
};

export default InfoCard;
